module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-button-button-group',\r\n  template: `\r\n    <h4>Basic</h4>\r\n    <nz-button-group>\r\n      <button nz-button>Cancel</button>\r\n      <button nz-button nzType=\"primary\">OK</button>\r\n    </nz-button-group>\r\n    <nz-button-group>\r\n      <button nz-button nzType=\"default\" disabled>L</button>\r\n      <button nz-button nzType=\"default\" disabled>M</button>\r\n      <button nz-button nzType=\"default\" disabled>R</button>\r\n    </nz-button-group>\r\n    <nz-button-group>\r\n      <button nz-button nzType=\"primary\" disabled>L</button>\r\n      <button nz-button nzType=\"default\" disabled>M</button>\r\n      <button nz-button nzType=\"default\">M</button>\r\n      <button nz-button nzType=\"dashed\" disabled>R</button>\r\n    </nz-button-group>\r\n    <h4>With Icon</h4>\r\n    <nz-button-group>\r\n      <button nz-button nzType=\"primary\"><i class=\"anticon anticon-left\"></i> Go back</button>\r\n      <button nz-button nzType=\"primary\">Go forward<i class=\"anticon anticon-right\"></i></button>\r\n    </nz-button-group>\r\n    <nz-button-group>\r\n      <button nz-button nzType=\"primary\"><i class=\"anticon anticon-cloud\"></i></button>\r\n      <button nz-button nzType=\"primary\"><i class=\"anticon anticon-cloud-download\"></i></button>\r\n    </nz-button-group>`,\r\n  styles  : [\r\n      `\r\n      h4 {\r\n        margin: 16px 0;\r\n        font-size: 14px;\r\n        line-height: 1;\r\n        font-weight: normal;\r\n      }\r\n\r\n      h4:first-child {\r\n        margin-top: 0;\r\n      }\r\n\r\n      [nz-button] {\r\n        margin-bottom: 12px;\r\n      }\r\n\r\n      nz-button-group {\r\n        margin-bottom: 8px;\r\n        margin-right: 8px;\r\n      }\r\n    `\r\n  ]\r\n})\r\nexport class NzDemoButtonButtonGroupComponent {\r\n}\r\n"